import { useEffect } from "react"
import { Link } from "react-router-dom"


const Home = () => {
    document.title = 'CodeGamer </>'
    useEffect(() => {
        document.querySelector('section').scrollTop = 0
    },[])

    return(
        <>
            <div className="section s_h" >
                <div className="Shower">
                    <div className="head">
                        <h1> 
                            CodeGamer &lt;/&gt;
                        </h1>
                    </div>
                    <div className="para">
                        <p>Wanna have some fun while programming??&nbsp;&nbsp;&nbsp;Then you visited the right place. Here you get interesting projects bulid with JavaScript, Python, React etc. 
                            <br /><br /><br />
                        <div className="head_links">
                            <Link to="/login" className="headlinks">Login</Link>
                            <Link to="/about" className="headlinks">About</Link>
                        </div>
                        </p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Home