import { Link } from "react-router-dom";

const Sidebar = ({isOpen,closeFunc}) => {
    let wi = window.innerWidth;

    let o = {
        left: '-25vw',
        height: 'calc(100vh - 50px)'
    }

    if(wi <= 850 && !isOpen){
        o = {
            left: '-100vw',
            height: '100vh'
        }
    }else if(wi <= 850 && isOpen){
        o = {
            left: '0',
            height: '100vh'
        }
    }
    else if(wi > 850 && !isOpen){o = {
        left: '-25vw',
        height: 'calc(100vh - 50px)'
    }}else if(wi > 850 && isOpen){
        o = {
            left: '0',
            height: 'calc(100vh - 50px)'
        }
    }

    return(
        <>
            <div style={o} className="sidebar">
                <div className="sideLogo"></div>
                <div className="sideNavs">
                    <Link onClick={closeFunc} to='/' className="Sidenav">Home 🏠</Link>
                    <Link onClick={closeFunc} to='/about' className="Sidenav">About &nbsp;<i className="fa-solid fa-circle-info"></i></Link>
                    <Link onClick={closeFunc} to='/projects' className="Sidenav">Projects 🧑‍💻</Link>
                    <Link onClick={closeFunc} to='/tutorials' className="Sidenav">Tutorials 🎓 </Link>
                    <Link onClick={closeFunc} to='/login' className="Sidenav">Login 👤 </Link>
                </div>
            </div>
        </>
    )
}

export default Sidebar