import React, { useEffect } from 'react'
import Editor from '../editor/Editor'

const Html = () => {

  useEffect(() => {
    document.title = 'HTML || CodeGamer</>'
    document.querySelector('section').scrollTop = 0;
  },[])  
  
  return (
        <>
            <div className="section s_h">
                <div className="Shower">
                    <div className="head">
                        <h1>HTML <span className="comment">&lt;/&gt;</span> Tutorials <br /> <span className="comment" style={{fontSize:'15px'}} >(Hyper Text Markup Language)</span> </h1>
                    </div>
                    <div className="para">
                        <p>
                            HTML <span className="comment"> (Hyper Text Markup Language)</span> is the most important thing to learn in order to make a website. Learning🎓 HTML is the very easy stuff. No website🕸️ can be made without HTML. You will have a great knowleade of HTML. Learn it and grow 📈.
                            <br /><br /><br />
                            <div className="head_links">
                                <a className='headlinks' href="#next">LEARN</a>
                                <a className='headlinks' href="/">TUTORIALS</a>
                            </div>
                        </p>
                    </div>
                </div>
            </div>
            <Editor lang='html' defaultVal='<h1> Simple Code Runner </h1>'/>
        </>
    )
}

export default Html