import { useEffect } from "react"

const ErrorPAge = () => {
    document.title = "404 not found 😟"
    useEffect(() => {
        document.querySelector('section').scrollTop = 0
    },[])


    return (
        <>
            <div className="section s_h" >
                <div className="Shower">
                    <div className="head">
                        <h1> 
                            <span className="comment">404</span> not found 😟
                        </h1>
                    </div>
                    <div className="para">
                        <p style={{color: "wheat",lineHeight: '30px',fontSize: '20px'}}> 
                            please check your search query 'cause it may be invalid .Since, there is no page available in this location. <br /> <b><i> "{document.location.pathname}" </i></b> was not found.
                        </p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ErrorPAge