import React ,{useEffect} from 'react'
import VanillaTilt from '../vanilla-tilt'

const Login = () => {

  useEffect(() => {
    document.querySelector('section').scrollTop = 0
    
    VanillaTilt.init(document.querySelector('.login_card'),{
      glare:true,
      "max-glare": 0.3
    })
    
  },[])

  document.title = "Login || CodeGamer </>"

  return (
    <>
        <div className="section login">
            <div className="login_card">
                <h2 className="login_head">LOG IN</h2>
            <form id='login_form'>
                <label htmlFor="username">Email :--</label>
                <input required type="text" id='username' autoComplete='off' />
                <label htmlFor="password">Pasword :-- </label>
                <input required type="password" id="password"  autoComplete='off' />
                <div className="remd">
                  <input type="checkbox" name="rem" id="rem" style={{cursor: 'pointer'}} />
                  <label htmlFor="rem" id='reml' style={{cursor: 'pointer'}}>remember me</label>
                </div>
                <br />
                <button type='submit'>LOG IN</button>
            </form>
            <span className="sign_up_text" style={{fontSize: '15px'}}>Dont have an account? <a href='/'>Sign up</a></span>
            </div>
        </div>
    </>
  )
}

export default Login