import React, { useEffect } from 'react'

import Prism from 'prismjs'
import { Link } from 'react-router-dom'

export default () => {

    document.title = 'Tutorials || CodeGamer </>'
    
    useEffect(() => {
        Prism.highlightAll()
        document.querySelector('section').scrollTop = 0;
    },[])

    return (
        <>
            <div className="section s_h">
                <div className="Shower">
                    <div className="head"><h1><span className="animateTexttype">Tutorial</span> <span className="comment">&lt;/&gt;</span></h1></div>
                <div className="para">
                        <p>Are you getting stuck while programming?? Then we are here to support and guide you in this journey. More you discover more you learn.You'll learn several languages like :-- HTML,CSS,JS,Python,C++ etc.etc 
                            <br /><br /><br />
                        <div className="head_links">
                            <a href="#HTML" className="headlinks">DISCOVER</a>
                            <Link to="/projects" className="headlinks">PROJECTS</Link>
                        </div>
                        </p>
                </div>
                </div>
            </div>
            <div className="section about doc tut" id='HTML'>
                <h1 className="component_head"> HTML </h1>
                <div className="tutcontent">
                    <div className="content">
                        <div className="plintro">
                            <h2>What is it ?? 🤔 </h2>
                            <br />
                            <p>HTML stands for Hyper Text Markup Language. It is a markup language used for making the basic structure of a webpage. Whitout HTML no website is possibe to be made <br /> <br />
                                <div className="head_links">
                                    <Link to="/tutorials/html" className="headlinks">Learn</Link><a href="" className="headlinks">Vlogs</a>
                                </div>
                            </p>

                        </div>
                        <div className='codeDisplaytut '>
                            <div className="code comment ">
                                <pre className='line-numbers'>
                                    <code className="language-html ">
                                        {
    `<html>
    <head>
        <title>This is head</title>
    </head>
    <body>
        <h1>This is a heading</h1>
        <p>This is a paragraph</p>
    </body>                                         
</html>`
                                        }
                                    </code>
                                </pre>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <hr />
            <div className="section about doc tut" id='CSS'>
                <br />
                <hr />
                <h1 className="component_head"> CSS </h1>
                <div className="tutcontent">
                    <div className="content">
                        <div className="plintro">
                            <h2>What is it ?? 🤔 </h2>
                            <br />
                            <p>CSS stands for Cascading Style Sheet. This is a stylesheet which is popularly used to design web pages. In CSS you select an element of HTML and add some properties to it. Before leartning it one should have a good knowledge of HTML <br /> <br />
                                <div className="head_links">
                                    <a href="" className="headlinks">Learn</a><a href="" className="headlinks">Vlogs</a>
                                </div>
                            </p>

                        </div>
                        <div className='codeDisplaytut'>
                            <div className="code comment">
                                <pre>
                                    <code className="language-css">
                                        {
    `p{
    color: red;
    margin-left: 20px;
}
section{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;                            
}`
                                        }
                                    </code>
                                </pre>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <hr />
            <div className="section about doc tut" id='JS'>
                <br />
                <hr />
                <h1 className="component_head"> JavaScript </h1>
                <div className="tutcontent">
                    <div className="content">
                        <div className="plintro">
                            <h2>What is it ?? 🤔 </h2>
                            <br />
                            <p>JavaScript is a programming which is mostly used to program a web page and make it dynamic. Now a days JavaScript is devloping with a huge acceleration and it is also being used from backend (server-side) programming. <br /> <br />
                                <div className="head_links">
                                    <a href="" className="headlinks">Learn</a><a href="" className="headlinks">Vlogs</a>
                                </div>
                            </p>

                        </div>
                        <div className='codeDisplaytut'>
                            <div className="code comment">
                                <pre>
                                    <code className="language-js">
                                        {
    `let elems = document.querySelectorAll('.elems');

elems.forEach(e => {
    e.addEventListener('click',ev => {
        ev.target.innerText = 'Text from JavaScript.'
    })
})
`
                                        }
                                    </code>
                                </pre>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <hr />
            <div className="section about doc tut" id='PYTHON'>
                <br />
                <hr />
                <h1 className="component_head"> Python </h1>
                <div className="tutcontent">
                    <div className="content">
                        <div className="plintro">
                            <h2>What is it ?? 🤔 </h2>
                            <br />
                            <p>Python is a general purpose programming language. There several things that can be done with python including web devlopment(server-side).Its powers are also utilised in AI devlopment, machine learning, GUI,Game devlopment etc.  <br /> <br />
                                <div className="head_links">
                                    <a href="" className="headlinks">Learn</a><a href="" className="headlinks">Vlogs</a>
                                </div>
                            </p>

                        </div>
                        <div className='codeDisplaytut'>
                            <div className="code comment line-numbers" >
                                <pre>
                                    <code className="language-js">
                                        {
`a = 10
b = 20

def sum(i,ii):
    return i + ii

print(sum(a,b))                                      
`
                                        }
                                    </code>
                                </pre>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
