import React from 'react'
import { Link } from "react-router-dom";


const Card = props => {
  return (
    <div data-tilt className="plcards">
    <div
        className="image data"
        style={{ backgroundImage: `url(${props.url})`, backgroundSize: `${props.bgS}` }}
    ></div>
    <div className="plcontent">
        <div className="plhead">{props.title}</div>
        <ul className="felist">
            {props.qualy.map(e => {
                return <li>{e}</li>
            })}

        </ul>
        <Link className="go" to={props.link}>
        {props.type}
        </Link>
    </div>
    </div>
  )
}

export default Card

