import React, { useEffect } from "react";
import VanillaTilt from '../vanilla-tilt';

//* importing images
import python_logo from './python_logo.png'
import cpplogo from './cpplogo.jpeg'
import react_logo from './react-logo.png'
import JavaScrpit_logo from './JavaScript-logo.png'


import Card from './templates/card'

const Projrct = () => {

  document.title = 'Project || CodeGamer </>'

  useEffect(() => {
      document.querySelectorAll('.plcards').forEach((elm) => {
        VanillaTilt.init(elm,{
          glare:true,
          'max-glare': 0.3
        })
      })
      document.querySelector('section').scrollTop = 0
  },[])

  return (
  <>
    <div className="section s_h">
      <div className="Shower">
        <div className="head">
          <h1>Projects &lt;/&gt;</h1>
        </div>
        <div className="para">
          <p>
            Wanna have some fun while programming??&nbsp;&nbsp;&nbsp;Then you
            visited the right place. Here you get interesting projects bulid
            with JavaScript, Python, React etc.
            <br />
            <br />
            <br />
            <div className="head_links">
              <a href="#plcards" className="headlinks">
                DO IT!
              </a>
              <a href="#next" className="headlinks">
                Discover
              </a>
            </div>
          </p>
        </div>
      </div>
    </div>
    <div className="section about doc" id="next">
      <h1 className="component_head">PROJECTS 🧑‍💻</h1>
      <br />
          <div className="content">
          <h2 className="sub_heading">Why are Projects necessary ? 🤔</h2>
          <br /><br />
              <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Doloribus accusantium ducimus neque, rerum expedita explicabo, repudiandae quibusdam nulla quo aliquid in asperiores quidem recusandae placeat, quis molestias unde sed obcaecati. Quam nemo eaque id libero ipsum dolore, impedit tempore molestiae voluptatibus sed. Obcaecati corrupti odit porro, iure rerum non minus, et quae, voluptate cupiditate harum eaque fugit! Ex tenetur excepturi neque eveniet accusamus quia, velit alias expedita nemo ratione. Cum natus fuga fugiat eum aspernatur nobis possimus saepe alias laudantium, placeat quidem accusantium excepturi, pariatur temporibus? Animi aliquam nemo eum quaerat itaque est odit, assumenda vitae quod! Dolorum, est commodi unde debitis ut quibusdam fugit corporis. Accusamus labore facilis obcaecati dolores cupiditate mollitia enim quae non natus. Quod quidem cum illum debitis. Dolorem eum, tempore nam, cupiditate repudiandae possimus, perferendis odit ratione iste fugit quaerat dicta natus! Dignissimos impedit voluptatibus totam in cum quo sit omnis deserunt. Tempora, suscipit tempore?</p>
          </div>
      </div>
      <hr />
      <div className="section about doc" id="plcards">
          <h1 className="component_head long" >In what "PL" are we going to build programs ? 🤔</h1>
          <div className="content flex">
              <Card url={python_logo} title="Python" qualy={['General Propgramming','Games','backend']} bgS="contain" type="discover" link="/projects/python" />
              <Card url={JavaScrpit_logo} title="JavaScript" qualy={['General Propgramming','Frontend','backend']} bgS="100% 100%" type="discover" link="/projects/javascript" />
              <Card url={cpplogo} title="C++" qualy={['General Propgramming','Games','DOS projects']} bgS="contain" type="discover" link="/projects/cpp" />
              <Card url={react_logo} title="React" qualy={['Frontend projects','Games','SPA applications']} bgS="contain" type="discover" link="/projects/react" />
          </div>
      </div>
  </>
);
};


export default Projrct