//* importing css 
import './styles/style.css';
import './styles/prism (1).css';

//* importing components
import Home from './components/home'
import Navbar from './components/navbar'
import Footer from './components/footer'
import Sidebar from './components/Sidebar'
import About from './components/about'
import Projects from './components/projects';
import ErrorPage from './components/ErrorPage'
import Login from './components/login'
import Tutorials  from './components/tutorials';
import Javascript from './components/projects/javascript';
import HtmlTut from './components/tutorials/html'

//* importing modules
import { useState } from 'react';
import {BrowserRouter,Routes, Route} from 'react-router-dom'

function App() {

  let [isSideOpen, setisSideOpen] = useState(false)
  
  var ind =  (
    

    <>
      <BrowserRouter >
      <Navbar sideOpen={() => {
        if(isSideOpen){setisSideOpen(false)}
        else{setisSideOpen(true)}
      }} isSideO={isSideOpen}/>
      <Sidebar closeFunc={() => {
        if(isSideOpen){setisSideOpen(false)}
        else{setisSideOpen(true)}}} isOpen={isSideOpen} />
      <section>
            <Routes>
              <Route exact path='/' element={<Home/>}></Route>
              <Route exact path='/about' element={<About/>}></Route>
              <Route exact path='/projects' element={<Projects/>}></Route>
              <Route exact path='/projects/javascript' element={<Javascript/>}></Route>
              <Route exact path='/login' element={<Login/>}></Route>
              <Route exact path='/tutorials' element={<Tutorials/>}></Route>
              <Route exact path='/tutorials/html' element={<HtmlTut/>}></Route>
              <Route path='*' element={<ErrorPage/>} ></Route>
            </Routes>
        <Footer/>
      </section>
      </BrowserRouter>
    </>
  );

  return ind
}

export default App;
