import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import VanillaTilt from '../../vanilla-tilt'


const Editor = ({lang,defaultVal}) => {
  
  const [html,setHtml] = useState(defaultVal)

  return (
    <>
      <div className="section about doc editor_holder" id='next'>
        <h1 className="component_head long">TryIt Editor <span className="comment" style={{color: '#777'}} >&lt;For {lang}/&gt;</span></h1>
        <div className="content">
          <div className="editor_area">
            <div className="editor_text" data-tilt-axis="y">
              <div className="lable">Editor <span className="comment">{'</>'}</span></div>
              <textarea onKeyDown={event => {
                  if (event.key === 'Tab') {
                    const start = event.target.selectionStart
                    const end = event.target.selectionEnd

                    event.target.value = event.target.value.substring(0, start) + '    ' + event.target.value.substring(end)

                    event.target.selectionStart = start+4
                    event.target.selectionEnd = end+4

                    
                    event.preventDefault()
                  }}}
                  onInput={e => setHtml(e.target.value)}
                  value={html}
              ></textarea>
            </div>
            <div className="output">
              <div className="lable">Output <span className="comment">{'</>'}</span></div>
              <iframe srcDoc={html} ></iframe>
            </div>
          </div>
        </div>
      
      <br /><br /><br />
      </div>
    </>
  )
}

export default Editor