
const Navbar = ({sideOpen,isSideO}) => {
    
    let exp = <i className="fa-solid fa-bars"></i>

    if(isSideO) {
        exp = <i style={{color: 'red'}} className="fa-solid fa-x"></i>
    }else{exp = exp}

    return(
        <>
        <div className="nav">
            <a href="/" className="logo"></a>
            <h3 className="nav_head">CodeGamers &lt;/&gt;</h3>
            {/* <span className="log-in-nav"> */}
                {/* <Link to="/login" className="login_link_nav"> */}
                    {/* <i className="fa-solid fa-address-book"></i> */}
                {/* </Link> */}
            {/* </span> */}
            <span className="open_menu" onClick={sideOpen}>{exp}</span>
        </div>
        </>
    )
}

export default Navbar