import React, { useEffect } from 'react'
import Prism from 'prismjs'

const Javascript = () => {
    
    useEffect(() => {
        document.querySelector('section').scrollTop = 0
        Prism.highlightAll()
    },[])

    document.title = 'Projects -- JS || CodeGamer </>'
  
    return (
    <>
        <div className="section s_h">
            <div className="Shower">
                <div className="head">
                    <h1>JavaScript projects<br /><span className="comment">{'</>'}</span></h1>
                </div>
                <div className="para">
                    <p>
                        You have visited one of the awesome page in this website. Here you'll find several projects buildt in JavaScript 😮. Enjoy them and try to create them by yourself altough the source code of the projects are provided. Code 🧑‍💻 and Grow 📈.
                    </p>
                </div>
            </div>
        </div>
        <hr />
        <div className="section about doc">
            <h1 className="component_head ">TOP CREATIONS</h1>
            <div className="content">
                
                <div className="project_tile" data-key="digital-clock">
                    <h2>1. Digital - Clock</h2>
                    <br /><br />
                    <p style={{paddingLeft: '25px'}}>
                        'Digital - Clock' :- It is a very easy project. Just a few lines of javascript will make this ready. Probably this is the first awesome project everybody makes with javascript. The main methods to be used in this project are <code className='language-javascript code'>setInterval()</code> and <code className="language-js code">new Date()</code>.
                    </p>
                </div>
            
            </div>
        </div>
    </>
  )
}

export default Javascript